 en la adquisición de competencias técnicas para su desempeño en el ámbito productivo. Este modelo pedagógico orienta la actuación de aprendices, instructores y la comunidad educativa en coherencia con los principios de la formación integral, garantizando calidad y pertinencia en los procesos educativos.")])])]),_c('div',{staticClass:"br1"},[_c('p',{staticClass:"mb-0"},[_vm._v("La elección del modelo pedagógico en una institución educativa influye directamente en su comunidad y en cómo se gestionan los procesos curriculares y evaluativos, lo que requiere que los docentes conozcan y apliquen dicho enfoque en su práctica diaria.")])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"cajon color-acento-contenido p-4 mb-5"},[_c('p',{staticClass:"mb-0"},[_vm._v("El desarrollo por competencias del SENA está fundamentado en la teoría del Aprendizaje Significativo de Ausubel y el modelo cognitivista de Piaget, que subrayan la importancia de las habilidades cognitivas, sociales y afectivas para el aprendizaje efectivo. A partir de esta teoría, surgió el concepto de competencias a finales del siglo XX, definiendo la capacidad de un individuo para interactuar de manera idónea en entornos productivos y sociales. Estas competencias se dividen en tres dimensiones: “Saber” (conocimiento), “Saber hacer” (desempeño) y “Saber ser” (actitud), con el objetivo de formar personas que contribuyan tanto al desarrollo empresarial como al progreso social.")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row justify-content-center mb-5"},[_c('div',{staticClass:"col-sm-12 col-lg-6"},[_c('div',{staticClass:"titulo-sexto color-secundario"},[_c('h5',[_vm._v("Figura 3.")]),_c('span',[_vm._v(" Elementos de la competencia")])]),_c('img',{attrs:{"src":require("@/assets/curso/temas/tema2/img16.svg"),"alt":"La imagen presenta un esquema jerárquico que organiza los elementos de l